import {NavController, Page, ActionSheet} from 'ionic-framework/ionic';
import {CaseListPage} from '../case-list/case-list';
@Page({
  templateUrl: 'build/pages/home/home.html'
})
export class HomePage {
    
    queryText:string;
    nav:NavController;
    constructor(nav:NavController) {
       this.queryText=''; 
       this.nav = nav;
    } 
    goToCaseList() {
        
        if(this.queryText!="")
        {
             this.nav.push(CaseListPage, this.queryText);
        }   
  }   
}



