import {Component}        from 'angular2/core';
import {HTTP_PROVIDERS,}  from 'angular2/http';
import {Observable}       from 'rxjs/Observable';
import {CaseItem} from '../../providers/caseItem';
import {WikipediaService} from '../../providers/wikipedia.service';

@Component({
  selector: 'my-wiki',
  template: `
    <h1>Wikipedia Demo</h1>
    <p><i>Fetches after each keystroke</i></p>

    <input #term (keyup)="search(term.value)"/>
    
    <ul>
      <li *ngFor="#item of items | async">{{item.headline}}</li>
    </ul>
  `,
  //templateUrl: '/pages/build/case-list/case-list.template.html',
  providers:[HTTP_PROVIDERS,, WikipediaService]
})
export class WikiComponent {

  constructor (private _wikipediaService: WikipediaService) {}

  items: Observable<CaseItem[]>;

  search (term: string) {
    this.items = this._wikipediaService.search(term);
  }
}


