import {NavController, Page, NavParams} from 'ionic-framework/ionic';
import {CaseData} from '../../providers/case-data';
import {CaseDetailPage} from '../case-detail/case-detail';
import {CaseItem} from '../../providers/caseItem';
import {forwardRef} from 'angular2/core';

import {bootstrap}         from 'angular2/platform/browser';
import {WikiComponent}        from '../case-list/wiki.component';


bootstrap(WikiComponent);

@Page({
    templateUrl: 'build/pages/case-list/case-list.html'
})
export class CaseListPage {
    nav: NavController;

    filterredCaseList: any;//filtered caselist in originalCaseList , it is subset of originalCaseList
   
    originalCaseList: any; // searched result by queryText, it is a subset of caseData.data
    errorMessage: any;

    queryText: string;
    savedQueryText: string;
    pet: string;
    hiddenContent: boolean;

    modalityConditions: any;
    caseData: CaseData;

    constructor(nav: NavController, navParams: NavParams, caseData: CaseData) {
        this.nav = nav;
        this.queryText = navParams.data;
        this.savedQueryText = this.queryText;
        let keyword: string = this.queryText;

        this.originalCaseList = [];
        this.modalityConditions = [];
        this.filterredCaseList = [];
        this.caseData = caseData;
        caseData.getAllCases().then(
            cases=> {
                this.originalCaseList = cases;
                this.filterredCaseList = cases;
                this.initializeConditions();
            },
            error=> this.errorMessage = <any>error);

        this.pet = "filterCondition";
        this.hiddenContent = true;
    }
    ngOnInit() {
        //this.getSearchCases();
        this.caseData.getAllCases().then(
            cases=> {
                this.originalCaseList = cases;
                this.filterredCaseList = cases;
                this.initializeConditions();
            },
            error=> this.errorMessage = <any>error);

    }

    getSearchCases(): void {
        let items: Array<CaseItem>;
        items = [];
        this.caseData.getSearchCases(this.queryText, 1, 1).then(cases=> {
            cases.forEach(element => {
                items.push(element);
            });
            this.originalCaseList = items;
            this.filterredCaseList = items;
            this.initializeConditions();
        }, function() {
            console.log("can not get the data from service");
        });
    }


    initializeConditions(): void {
        let index = 1;
        this.modalityConditions = [];

        this.originalCaseList.forEach(item=> {
            if (!this.isConditionExist(item)) {
                this.modalityConditions.push({
                    id: index++,
                    name: item.modality,
                    isChecked: false
                });
            }
        });
    }

    isConditionExist(caseItem: CaseItem): Boolean {

        for (var i = 0; i < this.modalityConditions.length; i++) {
            if (this.modalityConditions[i].name.indexOf(caseItem.modality) >= 0)
                return true;
        }
        return false;
    }

    isAddToCaseListByModalities(caseItem: CaseItem): Boolean {
        var isFilterNeeded = false;
        for (var i = 0; i < this.modalityConditions.length; i++) {
            if (this.modalityConditions[i].isChecked == true) {
                isFilterNeeded = true;
            }
        }
        if (!isFilterNeeded)
            return true;
        for (var i = 0; i < this.modalityConditions.length; i++) {
            if (this.modalityConditions[i].isChecked == true && this.modalityConditions[i].name.indexOf(caseItem.modality) >= 0)
                return true;
        }
        return false;
    }

    filterResultByConditions(): void {
        this.filterredCaseList = [];


        this.originalCaseList.forEach(item=> {
            if (this.isAddToCaseListByModalities(item)) {
                this.filterredCaseList.push(item);
            }
        });
    }

    filterCaseData(): void {

        this.filterResultByConditions();
        this.hiddenContentChange();
    }

    goToCaseDetail(onecase: CaseItem): void {
        this.nav.push(CaseDetailPage, onecase);
    }

    onCancel(): void {
        this.queryText = this.savedQueryText;
    }

    setCheckedInfo(id: number): void {
        this.modalityConditions[id - 1].isChecked = !this.modalityConditions[id - 1].isChecked;
    }

    hiddenContentChange(): void {
        this.hiddenContent = !this.hiddenContent;
    }

    resetConditions(): void {
        this.initializeConditions();
    }
}



