import {Injectable, Inject} from 'angular2/core';
import {CaseItem} from '../providers/caseItem'
import {ModalityCondition} from '../providers/ModalityCondition'
import {ResponseOptions, Response} from 'angular2/http';
import {Http} from 'angular2/http';
import {Observable, Subject, ReplaySubject} from 'rxjs/Rx';

@Injectable()
export class CaseData {
    http: Http;
    data: Array<CaseItem>;
    urlBase: string;

    constructor(http: Http) {
        // inject the Http provider and set to this instance
        this.http = http;
        this.urlBase = "http://161.92.157.247:8080/api/";

    }

    getAllCases() {
        return this.http.get(this.urlBase + "cases").toPromise()
            .then(res => <CaseItem[]>res.json().data, this.handleError)
            .then(data=> { console.log(data); return data; });
    }

    private handleError(error: Response) {
        // in a real world app, we may send the error to some remote logging infrastructure
        // instead of just logging it to the console
        console.error(error);
        return Observable.throw(error.json().error || 'Server error');
    }



    load(): Promise<CaseItem[]> {
        if (this.data) {
            // already loaded data
            return Promise.resolve(this.data);
        }
        //var url ="data/repository.json";
        //var url="http://161.92.157.247:8080/api/cases";
        // don't have the data yet
        // We're using Angular Http provider to request the data,
        // then on the response it'll map the JSON data to a parsed JS object.
        // Next we process the data and resolve the promise with the new data.
       
        return new Promise(resolve => {
            this.http.get(this.urlBase + "cases").subscribe(res =>
                this.data = res.json());
            resolve(this.data);
        });

    }

    getCases(): Promise<CaseItem[]> {
        return this.load().then(data=> {
            return data;
        });
    }

    getSearchCases(keyword: string, page: number, size: number): Promise<CaseItem[]> {
        var url = this.urlBase + "search/cases?keyword=" + keyword + "&page=" + page + "&size=" + size;
        return this.loadSearchCases(url).then(data=> {
            return data;
        });
    }


    loadSearchCases(url: string): Promise<CaseItem[]> {
        return new Promise(resolve => {
            this.http.get(url).subscribe(res =>
                this.data = res.json());
            resolve(this.data);
        });


    }
    
    // TODO: should get data from service 
    // return the group types grouped by modality, e.g. CT, MR, DXR, US
    getFilterConditionModality(keyword: string): ModalityCondition[] {

        return [{ "id": 1, "name": "CT", "isChecked": true },
            { "id": 2, "name": "MR", "isChecked": true }, ];
    }
    getCaseById(id: string): CaseItem {
        return null;
    }

    getCasesByKeywordWithPageAndSize(keyword: string, page: number, size: number): CaseItem[] {
        return null;
    }



}



